@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700;800&display=swap');

* {
  margin: 0;
  padding: 0;
  font-family: 'Poppins', sans-serif; }

body {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #77acee; }

.container {
  position: relative;
  z-index: 100; }

.card {
  position: relative;
  width: 600px;
  height: 850px;
  background: #e6f4f4;
  border-radius: 15px;
  overflow: hidden;
  margin: 10px; }

.content-1 {
  position: relative;
  width: 100%;
  height: 35%;
  background-image: linear-gradient(to bottom right, #0a52af, #1270ea);

  &::after {
    content: 'PLAY';
    position: absolute;
    left: 31%;
    top: 20%;
    font-size: 5.5em;
    font-weight: 800;
    color: #c1d4ec34;
    -webkit-text-fill-color: rgba(49, 108, 185, 0.231);
    -webkit-text-stroke-width: 2px; }

  .arrow-left {
    position: absolute;
    top: 8%;
    left: 6%;
    color: #e2e2e2;
    font-size: 20px;
    cursor: pointer; }

  span {
    position: absolute;
    top: 8%;
    right: 6%;
    color: #e2e2e2;
    font-size: 20px;
    cursor: pointer; } }

.heart i.fa-heart {
  color: #f00c0c; }

.content-1 .main-image img {
  width: 250px;
  height: 250px;
  position: absolute;
  top: 36%;
  left: 30%;
  z-index: 100; }

.content-2 {
  height: 120px;

  .sub-image {
    display: flex;
    justify-content: center;
    align-items: center;

    img {
      width: 30px;
      height: 30px;
      margin: 70px 7px 0px;
      padding: 5px;
      background: #fff;
      border-radius: 10px;
      box-shadow: -10px -10px 10px rgba(230, 244, 244, 0.5), 5px 5px 5px rgba(70, 70, 70, 0.12);
      cursor: pointer;
      border: 1.5px solid #fff;

      &:hover {
        border: 1.5px solid #0b67df; } } } }

.content-3 {
  position: relative;
  padding: 5px 20px 10px; }

.branding {
  span:nth-child(1) {
    font-size: 30px;
    font-weight: 500;
    margin-right: 60%; }

  .fa-download {
    font-size: 25px; }

  h4 {
    font-size: 18px;
    font-weight: 500;
    color: #464444; } }

.ratings span {
  display: inline-block;
  text-align: center;
  line-height: 26px;
  font-size: 20px;
  cursor: pointer;
  transition: all 0.5s;
  color: #fcba26;

  &:nth-child(5) {
    color: #a7a7a3; } }

.paragraph {
  p {
    font-size: 15px;
    color: #000;
    font-weight: 500; }

  span {
    color: #0b67df;
    font-weight: 700;
    cursor: pointer; } }

.price {
  display: flex;
  margin: 30px 0 10px; }

.space {
  flex: 1; }

.price {
  span {
    &:nth-child(1) {
      font-size: 20px;
      font-weight: 400;
      margin-top: 3px;
      margin-right: 5px; }

    font-size: 25px;
    font-weight: 600; }

  a {
    font-size: 12px;
    text-decoration: none;
    border: none;
    background-image: linear-gradient(to right, #0a52af, rgb(18, 112, 234));
    color: #fff;
    font-weight: 500;
    letter-spacing: 1px;
    padding: 10px 30px;
    border-radius: 15px;
    cursor: pointer; } }



@media  screen and ( max-width: 480px) {

  .card {
    width: 300px;
    height: 475px; }

  .content-1 .main-image img {
    left: 25%;
    width: 150px;
    height: 150px; }

  .branding {
    span:nth-child(1) {
      margin-right: 50%;
      font-size: 18px; }

    .fa-download {
     font-size: 20px; }

    h4 {
     font-size: 13px; } }

  .ratings span {
    font-size: 15px; }

  .paragraph {
    p {
      font-size: 13px; } }

  .content-1 {
      &::after {
        left: 12%;
        top: 15%; } }

  .content-2 {
    .sub-image {
      img {
        width: 17px;
        height: 17px; } } } }
